.drop-zone-container {
    background-color: #F2F2F2 !important;
    min-height: 128px;
    padding: 16px;
    position: relative;
    .cancelButton {
        position: absolute;
        right: 16px;
        top: 16px;
    }
    .imgContainer {
        height: 128px;
    }
    .dropZone {
        width: 100%;
        height: 100%;
    }
}