.tableRowHover {
    .ecoInitoatives-category-container {
        width: 16px;
        transition: width 0.5s;
        margin-left: 16px;
        .ecoInitoatives-category-img {
            height: 16px;
            margin-bottom: 14px;
        }
    }
    &:hover {
        .row-hover {
            background-color: #D8DEE2 !important;
        }
        .actionContainer {
            display: flex;
            visibility: visible;
            background-color: #F2F9FF;
        }
        .ecoInitoatives-category-container {
            width: 96px;
            margin-left: 0px;
            .ecoInitoatives-category-img {
                height: 45px;
                margin-bottom: 0px;
            }
        }
    }
    .actionContainer {
        background-color: #D8DEE2;
        width: 72px;
        height: 72px;
        position: absolute;
        right: 0;
        bottom: 0;
        visibility: hidden;
        transition: visibility 0s, background-color 0.5s linear;
    }
}

.campaignStatusButton {
    width: 88px;
    height: 24px;
    border-radius: 16px;
    text-transform: capitalize;
    &.draft {
        border: 1px solid #f5865e;
        color: #f5865e;
        &::after {
            content: "Draft !";
        }
        &:hover {
            border: #4960734d;
            background-color: #4960734d;
            color: #000;
            text-transform: uppercase;
            font-weight: 500;
            &::after {
                content: "Edit";
            }
        }
    }
    &.otherStatus {
        border: 1px solid #496073;
        color: #000000;
        &.scheduled {
            &::after {
                content: "Scheduled";
            }
            &:hover {
                &::after {
                    content: "CANCEL";
                }
            }
        }
        &.running {
            &::after {
                content: "Running";
            }
            &:hover {
                &::after {
                    content: "PAUSE";
                }
            }
        }
        &.paused {
            &::after {
                content: "Paused";
            }
            &:hover {
                &::after {
                    content: "START";
                }
            }
        }
        &:hover {
            border: #496073;
            background-color: #496073;
            color: #f7f7f7;
            text-transform: uppercase;
            font-weight: 500;
        }
    }
}

.border-type {
    border-left: 4px solid #FF9B76;
    border-radius: 4px 0px 0px 4px;
}